'use client';
//Input.tsx - UPDATED VERSION
import { useState, useRef, useEffect } from 'react';
import { FiSend } from 'react-icons/fi';
import { BsPaperclip, BsImage, BsFileEarmark } from 'react-icons/bs';
import { HiX, HiPhotograph } from 'react-icons/hi';

interface InputBarProps {
  onSendMessage: (message: string, files?: File[]) => void;
  disabled?: boolean;
  placeholder?: string;
  maxHeight?: number;
}

export default function InputBar({ 
  onSendMessage, 
  disabled = false,
  placeholder = "Message Freddie AI...",
  maxHeight = 200
}: InputBarProps) {
  const [input, setInput] = useState('');
  const [isFocused, setIsFocused] = useState(false);
  const [attachedFiles, setAttachedFiles] = useState<File[]>([]);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const imageInputRef = useRef<HTMLInputElement>(null);

  const adjustTextareaHeight = () => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = 'auto';
      textarea.style.height = `${Math.min(textarea.scrollHeight, maxHeight)}px`;
    }
  };

  useEffect(() => {
    adjustTextareaHeight();
  }, [input]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if ((input.trim() || attachedFiles.length > 0) && !disabled) {
      onSendMessage(input.trim(), attachedFiles);
      setInput('');
      setAttachedFiles([]);
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto';
      }
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || []);
    setAttachedFiles(prev => [...prev, ...files]);
  };

  const removeFile = (index: number) => {
    setAttachedFiles(prev => prev.filter((_, i) => i !== index));
  };

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
  };

  const getFileIcon = (file: File) => {
    if (file.type.startsWith('image/')) {
      return <HiPhotograph size={18} className="text-violet-400" />;
    }
    return <BsFileEarmark size={16} className="text-blue-400" />;
  };

  const getFileTypeLabel = (file: File) => {
    if (file.type.startsWith('image/')) {
      return 'Image';
    }
    const extension = file.name.split('.').pop()?.toUpperCase();
    return extension || 'File';
  };

  const getFileTypeColor = (file: File) => {
    if (file.type.startsWith('image/')) {
      return 'from-violet-600/50 to-purple-600/50 border-violet-600/50';
    }
    return 'from-blue-600/50 to-indigo-600/50 border-blue-600/50';
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      {/* Attached Files Display */}
      {attachedFiles.length > 0 && (
        <div className="mb-3 flex flex-wrap gap-2">
          {attachedFiles.map((file, index) => (
            <div
              key={index}
              className={`
                flex items-center gap-3 px-3 py-2.5 
                bg-gradient-to-r ${getFileTypeColor(file)}
                border-2 rounded-xl
                text-sm group hover:scale-[1.02] 
                transition-all shadow-lg
              `}
            >
              {/* File Icon */}
              <div className="flex-shrink-0">
                {getFileIcon(file)}
              </div>
              
              {/* File Info */}
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2 mb-0.5">
                  <span className={`
                    text-xs font-bold px-2 py-0.5 rounded-md
                    ${file.type.startsWith('image/') 
                      ? 'bg-violet-900/50 text-violet-200' 
                      : 'bg-blue-900/50 text-blue-200'
                    }
                  `}>
                    {getFileTypeLabel(file)}
                  </span>
                  <p className="text-slate-100 font-medium truncate max-w-[200px]">
                    {file.name}
                  </p>
                </div>
                <p className="text-slate-300 text-xs">
                  {formatFileSize(file.size)}
                </p>
              </div>
              
              {/* Remove Button */}
              <button
                type="button"
                onClick={() => removeFile(index)}
                className="p-1.5 hover:bg-white/10 rounded-lg transition-colors flex-shrink-0"
                title="Remove file"
              >
                <HiX size={16} className="text-slate-200 hover:text-white" />
              </button>
            </div>
          ))}
        </div>
      )}

      <div className={`
        flex items-end gap-2 p-2
        border-2 rounded-xl
        bg-slate-800/30
        transition-all duration-200
        ${isFocused 
          ? 'border-blue-500 shadow-lg shadow-blue-900/30' 
          : 'border-slate-700/50 hover:border-slate-600'
        }
        ${disabled ? 'opacity-50 cursor-not-allowed' : ''}
      `}>
        
        {/* Hidden File Inputs */}
        <input
          ref={fileInputRef}
          type="file"
          multiple
          className="hidden"
          onChange={handleFileSelect}
          accept="*/*"
        />
        <input
          ref={imageInputRef}
          type="file"
          multiple
          className="hidden"
          onChange={handleFileSelect}
          accept="image/*"
        />

        {/* Left Actions */}
        <div className="flex items-center gap-1 pb-1">
          <button
            type="button"
            disabled={disabled}
            onClick={() => fileInputRef.current?.click()}
            className="
              p-2 rounded-lg
              text-slate-400
              hover:text-blue-400 hover:bg-slate-700/50
              transition-all
              disabled:opacity-50 disabled:cursor-not-allowed
            "
            title="Attach file"
          >
            <BsPaperclip size={18} />
          </button>
          
          <button
            type="button"
            disabled={disabled}
            onClick={() => imageInputRef.current?.click()}
            className="
              p-2 rounded-lg
              text-slate-400
              hover:text-violet-400 hover:bg-slate-700/50
              transition-all
              disabled:opacity-50 disabled:cursor-not-allowed
            "
            title="Add image"
          >
            <BsImage size={18} />
          </button>
        </div>

        {/* Textarea */}
        <textarea
          ref={textareaRef}
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyDown}
          onFocus={() => setIsFocused(true)}
          onBlur={() => setIsFocused(false)}
          disabled={disabled}
          placeholder={placeholder}
          className="
            flex-1 
            py-2 px-2
            resize-none 
            bg-transparent 
            border-none 
            outline-none 
            focus:ring-0
            text-slate-100
            placeholder-slate-600
            text-base
            disabled:cursor-not-allowed
          "
          rows={1}
          style={{
            minHeight: '24px',
            maxHeight: `${maxHeight}px`,
            overflowY: input.length > 100 ? 'auto' : 'hidden'
          }}
        />

        {/* Send Button */}
        <button
          type="submit"
          disabled={(!input.trim() && attachedFiles.length === 0) || disabled}
          className={`
            p-2 mb-1 rounded-lg
            transition-all duration-300
            flex-shrink-0
            ${(input.trim() || attachedFiles.length > 0) && !disabled
              ? 'bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-500 hover:to-indigo-500 text-white shadow-md hover:shadow-lg' 
              : 'bg-slate-800/50 text-slate-700 cursor-not-allowed opacity-50'
            }
          `}
          title="Send message"
        >
          <FiSend size={18} />
        </button>
      </div>
    </form>
  );
}