'use client';
//MainLayout-WithPersistence.tsx
import { useState, useEffect } from 'react';
import Sidebar from './Sidebar';
import Chat from './Chat';
import ProjectsPage from './ProjectsPage';
import ImagesPage from './ImagesPage';
import SettingsPage from './SettingsPage';
import axios from 'axios';

export default function MainLayout() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [chats, setChats] = useState<any[]>([]);
  const [activeChat, setActiveChat] = useState<any>(null);
  const [modules, setModules] = useState<any[]>([]);
  const [selectedModule, setSelectedModule] = useState<any>(null);
  const [currentView, setCurrentView] = useState<'chat' | 'projects' | 'images' | 'settings'>('chat');

  // Load modules from backend and chats from localStorage on startup
  useEffect(() => {
    loadModules();
    loadChatsFromStorage();
  }, []);

  const loadModules = async () => {
    try {
      const response = await axios.get('http://localhost:8000/api/modules/installed/');
      setModules(response.data.modules);
      
      // Set first installed module as default
      const firstInstalled = response.data.modules.find((m: any) => m.installed);
      if (firstInstalled && !selectedModule) {
        setSelectedModule(firstInstalled);
      }
    } catch (error) {
      console.error('Error loading modules:', error);
      // Fallback to empty array if backend is down
      setModules([]);
    }
  };

  const loadChatsFromStorage = () => {
    try {
      const savedChats = localStorage.getItem('freddie_chats');
      if (savedChats) {
        const parsedChats = JSON.parse(savedChats);
        // Convert timestamp strings back to Date objects
        const chatsWithDates = parsedChats.map((chat: any) => ({
          ...chat,
          timestamp: new Date(chat.timestamp)
        }));
        setChats(chatsWithDates);
      }
    } catch (error) {
      console.error('Error loading chats from storage:', error);
    }
  };

  const handleToggleSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen);
  };

  const handleNewChat = () => {
    setActiveChat(null);
    setCurrentView('chat');
  };

  const handleSelectChat = (chat: any) => {
    setActiveChat(chat);
    setCurrentView('chat');
  };

  const handleSelectModule = (module: any) => {
    setSelectedModule(module);
  };

  const handleSaveChat = (chat: any) => {
    setChats(prevChats => {
      const existingIndex = prevChats.findIndex(c => c.id === chat.id);
      let updatedChats;
      
      if (existingIndex >= 0) {
        updatedChats = [...prevChats];
        updatedChats[existingIndex] = chat;
      } else {
        updatedChats = [chat, ...prevChats];
      }
      
      return updatedChats;
    });
    setActiveChat(chat);
  };

  const handleOpenImages = () => {
    setCurrentView('images');
  };

  const handleOpenProjects = () => {
    setCurrentView('projects');
  };

  const handleOpenSettings = () => {
    setCurrentView('settings');
  };

  const handleCloseSpecialView = () => {
    setCurrentView('chat');
  };

  const handleInstallModule = async (moduleId: string) => {
    try {
      const response = await axios.post('http://localhost:8000/api/modules/install/', {
        module_id: moduleId
      });
      console.log('Installation started:', response.data);
      
      // Show loading message
      alert(`Installing module... This may take a few minutes. Check the AI Models tab for progress.`);
      
      // Reload modules after a delay
      setTimeout(() => loadModules(), 5000);
    } catch (error) {
      console.error('Error installing module:', error);
      alert('Failed to install module. Please try again.');
    }
  };

  const handleUninstallModule = async (moduleId: string) => {
    try {
      const response = await axios.post('http://localhost:8000/api/modules/uninstall/', {
        module_id: moduleId
      });
      console.log('Uninstallation complete:', response.data);
      alert('Module uninstalled successfully!');
      
      // Reload modules
      loadModules();
    } catch (error) {
      console.error('Error uninstalling module:', error);
      alert('Failed to uninstall module. Please try again.');
    }
  };

  return (
    <div className="flex h-screen overflow-hidden bg-slate-950">
      {/* Show sidebar only in chat view */}
      {currentView === 'chat' && (
        <Sidebar
          isOpen={isSidebarOpen}
          onToggle={handleToggleSidebar}
          chats={chats}
          activeChat={activeChat}
          onNewChat={handleNewChat}
          onSelectChat={handleSelectChat}
          modules={modules}
          selectedModule={selectedModule}
          onSelectModule={handleSelectModule}
          onOpenImages={handleOpenImages}
          onOpenProjects={handleOpenProjects}
          onOpenSettings={handleOpenSettings}
        />
      )}

      {/* Main Content Area */}
      <div className="flex-1 overflow-hidden">
        {currentView === 'chat' && (
          <Chat
            toggleComponentVisibility={handleToggleSidebar}
            selectedModule={selectedModule}
            onSelectModule={handleSelectModule}
            modules={modules}
            activeChat={activeChat}
            onSaveChat={handleSaveChat}
          />
        )}
        
        {currentView === 'projects' && (
          <ProjectsPage onClose={handleCloseSpecialView} />
        )}
        
        {currentView === 'images' && (
          <ImagesPage onClose={handleCloseSpecialView} />
        )}
        
        {currentView === 'settings' && (
          <SettingsPage 
            onClose={handleCloseSpecialView}
            modules={modules}
            onInstallModule={handleInstallModule}
            onUninstallModule={handleUninstallModule}
          />
        )}
      </div>
    </div>
  );
}